// CORE styles

@SLIDE_CAROUSEL_ANIMATION: 1s linear;

smart-carousel {
  display: flex;
  position: relative;
  overflow: hidden;
  justify-content: center;

  [data-slides-area] {
    display: flex;
    position: relative;
    overflow: hidden;
    padding-bottom: 71px;
    width: 780px;
    z-index: 0;
  }

  smart-carousel-slide {
    display: none;
    position: relative;
    left: 0;
    box-sizing: border-box;
    min-width: 250px;
    min-height: 150px;
    margin: 0 5px;
    z-index: 1;
    background-color: aliceblue;
    transition: transform 1s linear;
  }

  [active] {
    display: block;
    position: relative;
    visibility: visible;
    z-index: 2;
    opacity: 1;
  }

  // Single Carousel Animation
  .left, .right {
    display: block;
  }
  .prev {
    position: absolute;
  }

  .left {
    animation: move-from-left @SLIDE_CAROUSEL_ANIMATION;
  }
  .left.prev {
    animation: move-form-active-to-left @SLIDE_CAROUSEL_ANIMATION;
  }
  .right {
    animation: move-from-right @SLIDE_CAROUSEL_ANIMATION;
  }
  .right.prev {
    animation: move-form-active-to-right @SLIDE_CAROUSEL_ANIMATION;
  }

  @keyframes move-from-left {
    from {
      left: -100%;
    }
    to {
      left: 0;
    }
  }
  @keyframes move-form-active-to-left {
    from {
      left: 0;
    }
    to {
      left: 100%;
    }
  }
  @keyframes move-from-right {
    from {
      left: 100%;
    }
    to {
      left: 0;
    }
  }
  @keyframes move-form-active-to-right {
    from {
      left: 0;
    }
    to {
      left: -100%;
    }
  }

  // Multi Carousel Animation
  &[strategy="multi"] smart-carousel-slide {
    display: block;
    transition: transform 1s linear;
  }
}
