smart-video {
  display: block;
}
.smart-video .sev-inner {
  display: block;
  width: 100%;
  height: 100%;
}
.smart-video iframe {
  border: 0;
}
.smart-video .video-js {
  height: 100%;
  width: 100%;
}
.img-container > smart-video {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}
smart-carousel button.arrow-prev,
smart-carousel button.arrow-next {
  display: none;
}
.smart-carousel button.arrow-prev,
.smart-carousel button.arrow-next {
  display: block;
  position: absolute;
  top: 50%;
  z-index: 2;
  width: 68px;
  height: 68px;
  margin-top: -69.5px;
  background: none;
  border: none;
  opacity: 0.5;
  cursor: pointer;
  -webkit-appearance: none;
  appearance: none;
}
.smart-carousel button.arrow-next {
  right: 0;
}
.smart-carousel button.arrow-prev {
  left: 0;
}
.smart-carousel .icon-arrow-right,
.smart-carousel .icon-arrow-left {
  display: block;
  height: 100%;
  background: url(data:image/svg+xml;base64,) no-repeat;
}
.smart-carousel .icon-arrow-right {
  transform: scaleX(-1);
}
.smart-carousel .smart-carousel-dots {
  position: absolute;
  width: 100%;
  height: auto;
  bottom: 30px;
  line-height: 0;
  text-align: center;
  opacity: 1;
  transition: opacity 0.5s ease-out;
  cursor: default;
}
.smart-carousel .carousel-dot {
  display: inline-block;
  width: 9px;
  height: 9px;
  margin: 0 5px;
  vertical-align: middle;
  background: rgba(118, 118, 118, 0.5);
  border: none;
  border-radius: 24px;
  cursor: pointer;
  outline: none;
}
.smart-carousel .active-dot {
  width: 11px;
  height: 11px;
  background: rgba(255, 255, 255, 0.5);
  border: 1px solid #bababa;
}
smart-carousel {
  display: flex;
  position: relative;
  overflow: hidden;
  justify-content: center;
}
smart-carousel [data-slides-area] {
  display: flex;
  position: relative;
  overflow: hidden;
  padding-bottom: 71px;
  width: 780px;
  z-index: 0;
}
smart-carousel smart-carousel-slide {
  display: none;
  position: relative;
  left: 0;
  box-sizing: border-box;
  min-width: 250px;
  min-height: 150px;
  margin: 0 5px;
  z-index: 1;
  background-color: aliceblue;
  transition: transform 1s linear;
}
smart-carousel [active] {
  display: block;
  position: relative;
  visibility: visible;
  z-index: 2;
  opacity: 1;
}
smart-carousel .left,
smart-carousel .right {
  display: block;
}
smart-carousel .prev {
  position: absolute;
}
smart-carousel .left {
  animation: move-from-left 1s linear;
}
smart-carousel .left.prev {
  animation: move-form-active-to-left 1s linear;
}
smart-carousel .right {
  animation: move-from-right 1s linear;
}
smart-carousel .right.prev {
  animation: move-form-active-to-right 1s linear;
}
@keyframes move-from-left {
  from {
    left: -100%;
  }
  to {
    left: 0;
  }
}
@keyframes move-form-active-to-left {
  from {
    left: 0;
  }
  to {
    left: 100%;
  }
}
@keyframes move-from-right {
  from {
    left: 100%;
  }
  to {
    left: 0;
  }
}
@keyframes move-form-active-to-right {
  from {
    left: 0;
  }
  to {
    left: -100%;
  }
}
smart-carousel[strategy="multi"] smart-carousel-slide {
  display: block;
  transition: transform 1s linear;
}
smart-image {
  display: block;
  width: 100%;
  height: 0;
  padding: 0;
  text-align: center;
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center center;
}
smart-image[mode='origin'] {
  display: inline-block;
  width: auto;
  height: auto;
}
smart-image[mode='cover'] {
  height: 100%;
}
smart-image[mode='save-ratio'] {
  height: 0;
}
smart-image[mode='save-ratio']:not([loaded]) {
  min-height: 1px;
}
smart-image:after {
  display: none;
  content: attr(alt);
}
smart-image[error]:after {
  display: inline-block;
}
smart-image[loaded]:after {
  display: none;
}
smart-image.bg-flex {
  background-size: 100% 100%;
}
smart-image.bg-inscribe {
  background-size: contain;
}
smart-image.bg-v-top {
  background-position-y: top;
}
smart-image.bg-v-center {
  background-position-y: center;
}
smart-image.bg-v-bottom {
  background-position-y: bottom;
}
smart-image.bg-h-left {
  background-position-x: left;
}
smart-image.bg-h-center {
  background-position-x: center;
}
smart-image.bg-h-right {
  background-position-x: right;
}
.img-container {
  position: relative;
  overflow: hidden;
}
.img-container smart-image {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  display: block;
}
.img-container smart-image[mode="origin"] {
  position: relative;
}
.img-container smart-image[mode='save-ratio'] {
  position: relative;
}
.img-container smart-image[lazy] {
  opacity: 0;
}
.img-container smart-image[lazy][ready] {
  opacity: 1;
  -webkit-transition: opacity 0.4s;
  transition: opacity 0.4s;
}
.img-container.img-container-no-rat smart-image {
  position: relative;
}
.img-container.img-container-1-1 {
  padding-top: 100%;
}
.img-container.img-container-1-1 smart-image {
  position: absolute;
}
.img-container.img-container-4-3 {
  padding-top: 75%;
}
.img-container.img-container-4-3 smart-image {
  position: absolute;
}
.img-container.img-container-16-9 {
  padding-top: 56.25%;
}
.img-container.img-container-16-9 smart-image {
  position: absolute;
}
